import { TarifsCard } from './TarifsCard';
import { useAppSelector } from '../../store/hook';

import beginnerTarifIcon from '../../assets/icons/lamp.svg';
import proTarifIcon from '../../assets/icons/darts.svg';
import businessTarifIcon from '../../assets/icons/laptop.svg';


export const Tarifs: React.FC = () => {
    const isLogged = useAppSelector((state) => state.auth.isLogged);

    const tarifsParams = [
        {
            id: 1,
            tarif_name: 'Beginner',
            description: '  ',
            img: beginnerTarifIcon,
            current: isLogged ? true : false,
            price: '799 ',
            old_price: '1 200 ',
            description_installment: ' 150 /.    24 .',
            tarif_option1: '  ',
            tarif_option2: ' ',
            tarif_option3: ' 24/7',
            button: isLogged ? '   ' : '',
            colors: {
                card: { bg: '#FFB64F', color: '#000000', border: isLogged ? '2px solid #FFB64F' : 'none' },
                button: isLogged ? { bg: '#D2D2D2', color: 'var(--black)' } : { bg: '#5970FF', color: '#FFFFFF' },
            }
        },
        {
            id: 2,
            tarif_name: 'Pro',
            description: ' HR  ',
            img: proTarifIcon,
            current: false,
            price: '1 299 ',
            old_price: '2 600 ',
            description_installment: ' 279 /.    24 .',
            tarif_option1: '   Beginner',
            tarif_option2: ' ',
            tarif_option3: '  ',
            button: '',
            colors: {
                card: { bg: '#7CE3E1', color: '#000000', border: 'none' },
                button: { bg: '#5970FF', color: '#FFFFFF' },
            }
        },
        {
            id: 3,
            tarif_name: 'Business',
            description: '  ',
            img: businessTarifIcon,
            current: false,
            price: '2 379 ',
            old_price: '3 700 ',
            description_installment: '',
            tarif_option1: '   Pro',
            tarif_option2: '  ',
            tarif_option3: ' ',
            button: '',
            colors: {
                card: { bg: '#000000', color: '#FFFFFF', border: 'none' },
                button: { bg: '#5970FF', color: '#FFFFFF' },
            }
        },
    ]

    return (
        <>
            {tarifsParams.map((item) => <TarifsCard key={item.id} {...item} />)}
        </>
    )
}